<template>
	<view class="board">
		<view class="box">
			<view class="title">对账记录</view>
			<searchBox :showCode="true" :showRange="true" :timeStr="timeStr" :showProject="true" :showOut="true"
				@handleSearch="handleSearch" @putout="handlePutout">
			</searchBox>
			<view class="table_wrap">
				<uni-table border stripe emptyText="暂无更多数据" style="height:698px;">
					<uni-tr style="background: #f6f6f6;">
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">账单编号</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">项目编号</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">项目名称</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">项目经理</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">结算人数</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">费用单价（元）</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">工作时长</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">结算总额（元）</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">状态</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">创建时间</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">操作时间</uni-th>
						<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">操作</uni-th>
					</uni-tr>
					<uni-tr v-for="(item,index) in list" :key="index">
						<uni-td align="center">{{item.code}}</uni-td>
						<uni-td align="center">{{item.project_code}}</uni-td>
						<uni-td align="center">{{item.project_name}}</uni-td>
						<uni-td align="center">{{item.broker_name}}</uni-td>
						<uni-td align="center">{{item.settlement_worker_count}}</uni-td>
						<uni-td align="center">{{item.price}}</uni-td>
						<uni-td align="center">{{item.work_hours}}</uni-td>
						<uni-td align="center">{{item.settlement_amount}}</uni-td>
						<uni-td
							align="center">{{status.filter(el => {return el.value == item.status})[0].text}}</uni-td>
						<uni-td align="center">{{item.create_time}}</uni-td>
						<uni-td align="center">{{item.update_time}}</uni-td>
						<uni-td align="center" style="padding:8px 0px;">
							<view class="ope flex flex_around">
								<view class="ope_item" @click="viewRecord(item)">详情</view>
							</view>
						</uni-td>
					</uni-tr>

				</uni-table>
				<pageBox :page="pagination" @toNext="toNext"></pageBox>
			</view>
		</view>
		<view class="mask" v-if="showMask">
			<view class="inner" v-if="showFirst">
				<view class="title">账单编号：{{currentBill.code}}</view>
				<view class="close" @click="close">
					<image src="/static/close_white.png" mode="widthFix"></image>
				</view>
				<view class="box">
					<view class="table_wrap">
						<uni-table border stripe emptyText="暂无更多数据" style="height:500px;">
							<uni-tr style="background: #f6f6f6;">
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">工人姓名</uni-th>
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">身份证号码</uni-th>
								<uni-th align="center" style="font-size: 14px;font-weight: 600;color:#333;">手机号</uni-th>

								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">工作时长</uni-th>
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">结算总额（元）</uni-th>
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">开始时间</uni-th>
								<uni-th align="center"
									style="font-size: 14px;font-weight: 600;color:#333;">结束时间</uni-th>
							</uni-tr>
							<uni-tr v-for="(item,index) in curCheckinDetail.list" :key="index">
								<uni-td align="center">{{item.worker_name}}</uni-td>
								<uni-td align="center">{{item.worker_id_card_number}}</uni-td>
								<uni-td align="center">{{item.worker_mobile}}</uni-td>
								<uni-td align="center">{{item.work_hours}}</uni-td>
								<uni-td align="center">{{item.settlement_amount}}</uni-td>
								<uni-td align="center">{{item.start_time}}</uni-td>
								<uni-td align="center">{{item.end_time}}</uni-td>
							</uni-tr>
						</uni-table>
						<pageBox :page="maskPagination" @toMaskNext="toMaskNext" :type="pageType"></pageBox>
					</view>
				</view>
			</view>
		</view>
	</view>
	</view>
</template>

<script>
	import searchBox from "@/components/search_box.vue"
	import pageBox from "@/components/paging_box.vue"
	import {
		saveAs
	} from 'file-saver'
	let app = getApp()
	export default {
		name: "commission_record",
		data() {
			return {
				pageType: "mask",
				timeStr: "上传",
				list: [],
				status: [{
						value: "waiting_settlement",
						text: "待结算"
					},
					{
						value: "settled",
						text: "已结算"
					},
					{
						value: "abandoned",
						text: "已作废"
					},
					{
						value: "voided",
						text: "已作废"
					},
				],
				pagination: {},
				showMask: false,
				showFirst: false,
				showSecond: false,
				allRise: 0,
				allDown: 0,
				seleAll: false,
				selectedIds: [],
				selectedIndex: [],
				selectedProjectIds: [],
				currentPage: 1,
				id: "",
				searCode: "",
				searPro: "",
				searStart: "",
				searEnd: "",
				curCheckinDetail: {},
				mergeList: [],
				currentBill: "",
				maskPageIndex: 1,
				maskPagination: {},
			};
		},
		components: {
			searchBox,
			pageBox
		},
		created() {
			let paramArr = []
			if (location.href.indexOf("?") != -1) {
				paramArr = location.href.split("?")[1].split("&")
			}
			for (var i = 0; i < paramArr.length; i++) {
				if (paramArr[i].indexOf("status") != -1) {
					let status = paramArr[i].split("=")[1]
					this.currentTab = this.tabs.filter(el => {
						return el.value == status
					})[0]
				}
			}
			this.getList()
		},
		methods: {
			toNext(e) {
				this.currentPage = e
				this.getList()
			},
			toMaskNext(e) {
				this.maskPageIndex = e
				this.getDetailList()
			},
			handlePutout(e) {
				this.searStart = e.startTime
				this.searEnd = e.endTime
				this.searCode = e.code
				this.searPro = e.projectName
				let _this = this
				let header = {
					"Authorization": "bearer " + uni.getStorageSync("token"),
					"accept": "application/json"
				}
				let url = app.globalData.baseUrl + "/admin/export/project-bills/settled?bill=" + this.searCode +
					"&project=" + this.searPro + "&create_time_start=" + this.searStart + "&create_time_end=" + this
					.searEnd + "&page=" + this.currentPage
				uni.downloadFile({
					url: url,
					header: header,
					success: (res) => {
						console.log(res)
						if (res.statusCode === 200) {
							let date = new Date()
							let time = date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + date.getDate()
							saveAs(res.tempFilePath, "对账记录" + time + ".xlsx");
						}
					}
				})
			},
			changeTab(item) {
				this.currentTab.value = item.value
				this.currentTab.text = item.text
				this.setPageName({
					pageName: "checkin_list"
				}, "", item.value)
				this.getList()
			},
			navigate(obj, id) {
				this.setPageName(obj, id)
			},
			viewRecord(item) {
				this.currentBill = item
				this.getDetailList()
			},
			getDetailList() {
				let url = "/admin/project-bill/" + this.currentBill.id + "/info?page=" + this.maskPageIndex
				this.$request(url).then(res => {
					if (res.code == 0) {
						this.curCheckinDetail = res.data.list
						this.showMask = true
						this.showFirst = true
						this.maskPagination = res.data.pagination
					}
				})
			},
			getList() {
				uni.showLoading({
					title: "努力加载中~"
				})
				let url = "/admin/project-bills/settled?bill=" + this.searCode +
					"&project=" + this.searPro + "&create_time_start=" + this.searStart + "&create_time_end=" + this
					.searEnd + "&page=" + this.currentPage
				this.$request(url, {}, "GET", this.header).then(res => {
					if (res.code == 0) {
						this.list = res.data.list
						this.pagination = res.data.pagination
					}
				})
			},
			handleSearch(e) {
				this.searCode = e.code
				this.searPro = e.projectName
				this.searStart = e.startTime
				this.searEnd = e.endTime
				this.getList()
			},
			close() {
				this.showMask = false
				this.showFirst = false
				this.showSecond = false
			},

		}
	}
</script>

